<template>
  <div id="common_bottom">
    <div class="common_bottom_text">
      <slot name="bottom_text">{{num}}</slot>
    </div>
    <van-button
    :class="{common_bottom_btn: true, disabledBtn}"
     @click="handleBtn"
     type="default"
     :disabled="saveDisabled"
     >{{text.toUpperCase()}}</van-button>
     <!-- UI 调整字体为大写 -->
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'commonBottom',
    // props: ["text","num","regionAllData", "disabledBtn"],
    props:{
      saveDisabled:{
        type:Boolean,
        default:false
      },
      text:{
      },
      num:{
      },
      disabledBtn:{
      },
      regionAllData:{
      }
    },
    data(){
      return{
        aaa:'asa'
      }
    },
    methods: {
      handleBtn:function(){
        if(this.disabledBtn){
          return
        }
        this.$emit("btnHandle")
      }
    }
  }
</script>
<style>
  #common_bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 75px;
    background: #fff;
    -webkit-box-shadow: 0 0 6px rgba(0,0,0,0.15);
    -moz-box-shadow: 0 0 6px rgba(0,0,0,0.15);
    -ms-box-shadow: 0 0 6px rgba(0,0,0,0.15);
    -o-box-shadow: 0 0 6px rgba(0,0,0,0.15);
    box-shadow: 0 0 6px rgba(0,0,0,0.15);
    z-index: 50;
    font-family: ProximaNova-Extrabold;
    font-weight: 900;
  }
  #common_bottom .common_bottom_btn {
    position: fixed;
    bottom: 20px;
    right: 20px;
    height:35px;
    line-height:35px;
    text-align: center;
    font-size: 14px;
    font-weight: bold;
    color: #fff;
    background: #00b0a9;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    border-radius: 5px;
    cursor: pointer;
  }
  #common_bottom .disabledBtn{
    background: #f3f3f3;
    color: #999;
  }
  #common_bottom .common_bottom_text {
    position: fixed;
    bottom: 0;
    left: 32px;
    height: 75px;
    line-height: 75px;
    font-size: 16px;
    font-weight: bold;
    color: #000;
  }
</style>



